* {
  font-family: Montserrat, sans-serif;
}

.packName {
  display: flex;
  align-items: center;
  justify-content: space-between;

  max-height: 50px;
  margin-top: 15px;
}
.packNameTitle {
  display: flex;
  align-items: center;

  font-size: 22px;
  font-weight: 600;
  line-height: 27px;
}
.packNameTitleSpan {
  overflow: hidden;

  text-overflow: ellipsis;
  white-space: nowrap;
}
.search {
  width: 100%;
  margin-top: 30px;
}
.searchSpan {
  display: inline-block;

  margin-bottom: 12px;

  font-size: 14px;
  font-weight: 600;
  line-height: 17px;
}
.input {
  width: 100%;
}

.btnTitle {
  font-size: 16px;
  font-weight: 500;
  line-height: 20px;
  text-align: center;
  text-transform: lowercase;
}
.btnTitle:first-letter {
  text-transform: uppercase;
}
