.bigred{
    font-size: 20px;
    color: red;
}
.main {
    display: flex;
    justify-content: center;


    margin-top: 120px;
}
.register {
    width: 413px;
    height: 528px;
    padding: 35px 33px 42px;

    text-align: center;

    background: #FFFFFF;
    border-radius: 2px;
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1), -1px -1px 2px rgba(0, 0, 0, 0.1);
}

h1 {
    margin: 0 0 41px;

    font-family: 'Montserrat', sans-serif;
    font-size: 26px;
    font-weight: 600;
    font-style: normal;
    line-height: 32px;
    text-align: center;
}

.submitButton {
    display: flex;
    align-items: center;
    justify-content: center;


    margin-top: 60px;
    margin-bottom: 31px;
}
.button {
    width: 347px;
    height: 36px;
    padding-top: 8px;
    padding-bottom: 8px;


    background-color: #366EFF;
    box-shadow: 0 4px 18px rgba(54, 110, 255, 0.35), inset 0 1px 0 rgba(255, 255, 255, 0.3);
}
.button span {
    font-size: 16px;
    font-weight: 500;
    font-style: normal;
    line-height: 20px;
    color: #FFFFFF;
    text-align: center;
    text-transform: none;


}
.question {
    align-items: center;


    height: 24px;
    margin-bottom: 11px;

    font-family: 'Montserrat', sans-serif;
    font-size: 14px;
    font-weight: 600;
    line-height: 24px;
    color: #000000;

    opacity: 0.5;
}
.link {
    font-size: 16px;
    font-weight: 600;
    font-style: normal;
    line-height: 24px;
    color: #366EFF;
    text-align: center;
    text-decoration-line: underline;
}
.field {
    display: flex;


    width: 347px;
    height: 48px;

}
.error {
    position: absolute;

    font-size: 12px;
    color: indianred;
    text-align: end;
}
.visibility {
    margin-right: 15px;
    margin-bottom: 5px;
}
.iconStyle {
    margin-right: 5px;
    margin-bottom: 9px;
}